:root {
  --primary-font: "Inter", sans-serif;
  --secondary-font: "Lexend Deca", sans-serif;
  --white: #fff;
  --transparent-white-paragraph: #ffffffbf;
  --transparent-white-headings: #fff9;
  --very-dark-blue: #090b1a;
  --dark-desaturated-blue: #1b1938;
  --soft-violet: #aa5cdb;
  --mobile-bg-header: url("image-header-mobile.fca6621f.jpg");
  --desktop-bg-header: url("image-header-desktop.725b6d2f.jpg");
}

.container {
  background-color: var(--very-dark-blue);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 20rem;
  min-height: 100vh;
  display: flex;
}

.card {
  background-color: var(--dark-desaturated-blue);
  font-family: var(--primary-font);
  color: var(--white);
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  flex-direction: column;
  width: 100%;
  max-width: 20.4375rem;
  font-weight: 400;
  display: flex;
}

.card .card__header {
  background-image: var(--mobile-bg-header);
  background-color: var(--soft-violet);
  background-blend-mode: multiply;
  mix-blend-mode: plus-lighter;
  background-repeat: no-repeat;
  background-size: cover;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  height: 15rem;
}

.card .card__content {
  text-align: center;
  width: 100%;
  padding: 15px 25px;
}

.card .card__content .card__heading {
  font-size: 1.75rem;
  font-weight: 700;
  line-height: 2.75rem;
}

.card .card__content .card__highlight {
  color: var(--soft-violet);
}

.card .card__content .card__description {
  color: var(--transparent-white-paragraph);
  font-size: 1rem;
  line-height: 1.5em;
}

.card .card__content .card__stats {
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.card .card__content .card__stats .stats__figure {
  font-size: 1.5rem;
  font-weight: 700;
}

.card .card__content .card__stats .stats__heading {
  font-family: var(--secondary-font);
  color: var(--transparent-white-headings);
  text-transform: uppercase;
  letter-spacing: .125em;
  margin-top: -15px;
  font-size: .75rem;
  font-weight: 400;
}

@media screen and (min-width: 1024px) {
  .card {
    border-top-left-radius: 12px;
    border-bottom-right-radius: 0;
    flex-direction: row-reverse;
    max-width: 70rem;
  }

  .card .card__header {
    background-image: var(--desktop-bg-header);
    border-top-left-radius: 0;
    border-bottom-right-radius: 12px;
    width: 34rem;
    height: 27.9375rem;
  }

  .card .card__content {
    text-align: left;
    width: 36rem;
    padding: 45px 80px;
  }

  .card .card__content .card__heading {
    font-size: 2.2rem;
  }

  .card .card__content .card__stats {
    flex-direction: row;
    width: 21.75rem;
    margin-top: 50px;
  }
}

